.contact-content {
  background: #181c28 url("../../static/background-contact.jpg") no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-options {
  width: 80%;
  height: 80%;
  padding: 26px;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: space-between;
  align-content: space-between;
  flex-wrap: wrap;
  border-radius: 24px;
  overflow-y: auto;
}

.contact-options-item {
  background: rgb(17, 153, 55);
  cursor: pointer;
  color: white;
  margin: 0 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 46px;
  height: 20%;
  width: 40%;
  border-radius: 12px;
}

.contact-options-item a {
  color: inherit;
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-options-item:hover {
  color: rgb(17, 153, 55);
  background: white;
}

@media (max-width: 414px) {
  .contact-options {
    margin-top: auto;
    width: 95%;
    height: 90%;
  }
  .contact-options-item {
    width: 100%;
    margin: 0;
    margin-bottom: 24px;
  }
  .contact-options-item p {
    font-size: 16px;
  }
}
